.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.me-image {
  display: block;
  max-width: 448px;
  max-height: 672px;
  width: auto;
  height: auto;
  clip-path: url(#short)
}

.wrapper {
  position: relative;
}

.short {
  position: absolute;
  top: 0;
  left: 0;
  filter: hue-rotate(45deg) saturate(50%);
  animation: short-spin infinite 60s linear;
}

@keyframes short-spin {
  0% {
    filter: hue-rotate(45deg) saturate(60%);
  }
  50% {
    filter: hue-rotate(405deg) saturate(100%);
  }
  100% {
    filter: hue-rotate(810deg) saturate(60%);
  }
}